<template lang="pug">

.settings
	.settings-item.settings-exit(@click="logout") Выйти

</template>

<script>
export default {
	methods: {
		logout(){
			this.$store.dispatch('profile/logout')
			this.$router.push('/')
		}
	}
}
</script>

<style lang="stylus" scoped>

.settings
	&-item
		width 30%
		box-shadow 0 0 6px 0 rgba(#000, .1)
		border-radius 10px
	&-exit
		background-color lighten(red, 12)
		font-size 20px
		font-weight 600
		color #fff
		min-height 120px
		text-align center
		line-height 120px
		cursor pointer
		transition background-color .2s
		&:hover
			background-color lighten(red, 20)
		&:active
			background-color lighten(red, 25)


</style>